import React, { Component } from 'react';
import { Row, Col, Form, InputNumber } from 'antd';
import { withRouter } from 'react-router';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'FormSearchDormitoryRoom' })
class FormSearchBill extends Component {
  state = {};

  formItemLayout = 6;

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.onSearch(values.fiscalYear);
      }
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={this.formItemLayout}>
            <Form.Item label="Năm tài chính">
              {getFieldDecorator('fiscalYear', {initialValue: new Date().getFullYear()})(
                <InputNumber />
              )}
            </Form.Item>
          </Col>

          <Col span={this.formItemLayout}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default withRouter(FormSearchBill);



