import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message, Checkbox } from 'antd';
import { deleteBill, searchBill } from 'services/bill';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import FormSearchBill from './FormSearchBill';

import './style.scss';

class Bill extends Component {
  state = {
    reload: 0,
    searchKey: new Date().getFullYear(),
  };

  onSearch = searchKey => {
    this.setState({ searchKey, reload: this.state.reload + 1 });
  };

  handleDelete = async id => {
    await deleteBill(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    console.log('search Key', this.state.searchKey);
    const columns = [
      {
        title: 'Tên loại hóa đơn',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Ký hiệu mẫu hóa đơn',
        dataIndex: 'invoiceTemplate',
        key: 'invoiceTemplate',
        align: 'center',
      },
      {
        title: 'Ký hiệu hóa đơn',
        dataIndex: 'invoiceCode',
        key: 'invoiceCode',
        align: 'center',
      },
      {
        title: 'Năm tài chính',
        dataIndex: 'fiscalYear',
        key: 'fiscalYear',
        align: 'center',
      },
      {
        title: 'Còn hiệu lực',
        dataIndex: 'isActive',
        key: 'isActive',
        align: 'center',
        render: (text, record) => <Checkbox checked={record.isActive} />,
      },
      {
        title: 'Loại hóa đơn',
        dataIndex: 'configType',
        key: 'configType',
        align: 'center',
      },
      {
        title: 'Số tồn đầu kỳ từ',
        dataIndex: 'previousRemainFrom',
        key: 'previousRemainFrom',
        align: 'center',
      },
      {
        title: 'Số tồn đầu ký đến',
        dataIndex: 'previousRemainTo',
        key: 'previousRemainTo',
        align: 'center',
      },
      {
        title: 'Số phát hành trong kỳ từ',
        dataIndex: 'publishFrom',
        key: 'publishFrom',
        align: 'center',
      },
      {
        title: 'Số phát hành trong kỳ đến',
        dataIndex: 'publishTo',
        key: 'publishTo',
        align: 'center',
      },
      {
        title: 'Số hóa đơn xóa',
        dataIndex: 'noOfDelete',
        key: 'noOfDelete',
        align: 'center',
      },
      {
        title: 'Danh sách các số hóa đơn xóa',
        dataIndex: 'deletedReceipt',
        key: 'deletedReceipt',
      },
      {
        title: 'Số hóa đơn mất',
        dataIndex: 'noOfLost',
        key: 'noOfLost',
        align: 'center',
      },
      {
        title: 'Danh sách số hóa đơn mất',
        dataIndex: 'lostReceipt',
        key: 'lostReceipt',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        className: 'action-column',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__TAI_CHINH__QUAN_LY_HOA_DON__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Hóa đơn">
        <CartHeadAction>
          <Link to={routeConstant.DANH_MUC__TAI_CHINH__QUAN_LY_HOA_DON__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchBill onSearch={this.onSearch} />
          </Col>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={() => searchBill({ fiscalYear: this.state.searchKey })}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <Bill />
  </Page>
);



